#alpheios-panel, #alpheios-popup {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif !important;
  font-size: 16px !important;
  color: #333333 !important;
  background: #ffffff !important;
  border-color: 1px solid #d3d3d3 !important;
  box-sizing: border-box !important;
  overflow: auto !important;
  margin: 0 !important; }
  #alpheios-panel h1, #alpheios-panel h2, #alpheios-panel h3, #alpheios-panel h4, #alpheios-panel h5, #alpheios-panel h6, #alpheios-panel p, #alpheios-panel span, #alpheios-panel a, #alpheios-popup h1, #alpheios-popup h2, #alpheios-popup h3, #alpheios-popup h4, #alpheios-popup h5, #alpheios-popup h6, #alpheios-popup p, #alpheios-popup span, #alpheios-popup a {
    font-family: inherit !important;
    background: transparent !important;
    color: #333333 !important;
    line-height: 1.15 !important;
    -webkit-text-size-adjust: 100% !important;
    padding: 0 !important; }
  #alpheios-panel button,
  #alpheios-panel input,
  #alpheios-panel optgroup,
  #alpheios-panel select,
  #alpheios-panel textarea, #alpheios-popup button,
  #alpheios-popup input,
  #alpheios-popup optgroup,
  #alpheios-popup select,
  #alpheios-popup textarea {
    font-family: inherit !important;
    font-size: 100% !important;
    line-height: 1.15 !important;
    margin: 0 !important; }
  #alpheios-panel button,
  #alpheios-panel select, #alpheios-popup button,
  #alpheios-popup select {
    text-transform: none !important; }
  #alpheios-panel button,
  #alpheios-panel [type="button"],
  #alpheios-panel [type="reset"],
  #alpheios-panel [type="submit"], #alpheios-popup button,
  #alpheios-popup [type="button"],
  #alpheios-popup [type="reset"],
  #alpheios-popup [type="submit"] {
    -webkit-appearance: button !important; }
  #alpheios-panel textarea, #alpheios-popup textarea {
    overflow: auto !important; }
  #alpheios-panel [type="checkbox"],
  #alpheios-panel [type="radio"], #alpheios-popup [type="checkbox"],
  #alpheios-popup [type="radio"] {
    box-sizing: border-box !important;
